
@charset "UTF-8";
.page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #EAEBEC;
}
.page > main {
    flex-grow: 1;
}
.page > main {
    min-width: 1024px;
    max-width: 1440px;
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    width: 100%;
    /* 试试不加这句会怎样 */
    align-self: center;
}
#resumeEditor {
  width: 35%;
  background: #444;
}
#resumePreview {
  flex-grow: 1;
  margin-left: 16px;
  background: #777;
}
svg.icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size: 16px;
}



